	import uCharts from '@/utils/u-charts.js';
	var _self;
	var canvasRing=null;
   
	export default {
		props:['shop'], 
		data() {
			return {
				cWidth:'',
				cHeight:'',
				pixelRatio:1,
				serverData:'',
			}
		},
		watch: {
			shop () {
				if(canvasRing && canvasRing.updateData) {
					canvasRing.updateData({
						series: this.shop.series,
						title: {
							name: this.shop.title,
							color: '#191f25',
							fontSize: uni.upx2px(54)*_self.pixelRatio,
							offsetY:uni.upx2px(-20)*_self.pixelRatio,
						},
					});
					return;
				}
      this.showMix("canvasRing",this.shop);
			}
		}, 
		created() {
			_self = this;
			this.cWidth=uni.upx2px(660);
			this.cHeight=uni.upx2px(428);
			this.showRing ('canvasRing', this.shop)
		},
		methods: {
			showRing(canvasId,chartData){
				canvasRing=new uCharts({
					$this:_self,
					canvasId: canvasId,
					type: 'ring',
					fontSize:uni.upx2px(11),
					animation: false, 
					legend:{
						show: false
					}, 
					title: {
						name: chartData.title,
						color: '#191f25',
						fontSize: uni.upx2px(54)*_self.pixelRatio,
						offsetY:uni.upx2px(-20)*_self.pixelRatio,
					},
					subtitle: {
						name: '门店销售',
						color: '#a3a5a8',
						fontSize: uni.upx2px(28)*_self.pixelRatio,
						//offsetY:uni.upx2px(0)*_self.pixelRatio,
					},
					extra: {
						pie: {
						  offsetAngle: 0,
						  ringWidth: uni.upx2px(30)*_self.pixelRatio,
						  layoutWidth:uni.upx2px(15)
						}
					},
					background:'#FFFFFF',
					pixelRatio:_self.pixelRatio,
					series: chartData.series,
					animation: true,
					width: _self.cWidth*_self.pixelRatio,
					height: _self.cHeight*_self.pixelRatio,
					disablePieStroke: true,
					dataLabel: false,
				});
			},
		}
	}
